
  import Vue from 'vue'

  export default Vue.extend({
    name: 'loginScreen',

   data: () => ({
      account:"",
      password:"",
      show1:false,
      showDialog:false,
      verificationNumber:"",
      showPassword:false,
      password1:"",
      password2:"",
      userList:[
        {
          account:"admin",
          password:"admin",
          name:"王小明"
        },
        {
          account:"test",
          password:"test",
          name:"陳之明"
        },
      ],
      member:{
        account:"",
      }
    }),
    methods:{
      login(){
        const result = this.userList.filter(user => user.account==this.account && user.password ==this.password );
        if(result.length){
          this.$router.push("/main?name="+result[0].name)
        }
        else{
          alert("帳號密碼有誤")
        }
      },
      editAction(){
        if(this.member.account==""){
          alert("請輸入帳號")
        }
        else{
          let check = this.userList.filter(user => user.account ==this.member.account)
          if(check.length){
            alert("請到驗證的信箱取得驗證碼")
          }
          else{
            alert("帳號輸入錯誤")
          }
        }

      },
      submit(){
        this.showPassword = true
      },
      modifypassword(){
        if(this.password1!=this.password2){
          alert("輸入的密碼互不相同")
        }
        else{
          alert("修改成功")
          this.showDialog=false
          this.showPassword=false
        }
      }
    },
  })
