<template>
  <v-app id="inspire">
    <v-app-bar
      app
    >
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>{{this.$route.query.name}}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main>
      <v-container>
        <v-row>
          <v-col
            v-for="n in imgList"
            :key="n.src"
            cols="12" md="4" xl="3" sm="6"
          >
            <v-card
                class="mx-auto"
                max-width="400"
            >
                <v-img
                    class="white--text align-end"
                    height="200px"
                    :src="n.src"
                >
                <v-card-title>{{n.title}}</v-card-title>
                </v-img>

                <v-card-subtitle class="pb-0">
                    {{n.content}}
                </v-card-subtitle>

                <v-card-text class="text--primary">
                <div>{{n.description}}</div>
                </v-card-text>

                <v-card-actions>
                <v-btn
                    color="success"
                    text
                    @click="show(n)"
                >
                    <v-icon>
                        mdi-eye
                    </v-icon>
                </v-btn>


                </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  export default {
    name: 'FrontPage',
    data: () => ({
      drawer: true,
        imgList: [
          { src:"https://picsum.photos/510/200?random" ,title:"showImage",content:"Image",description:"Image",uuid:"1",show:"Image"},
          { src:"https://picsum.photos/510/500?random" ,title:"showholiday",content:"holiday",description:"holiday",uuid:"2",show:"holiday"},
          { src:"https://picsum.photos/510/300?random",title:"showtable",content:"table",description:"table",uuid:"3",show:"table"},
          { src:"https://picsum.photos/500/300?random",title:"showBMI",content:"BMI",description:"BMI",uuid:"4",show:"BMI"},
          { src:"https://picsum.photos/520/300?random",title:"showCAL",content:"CAL",description:"CAL",uuid:"5",show:"CAL"},
          { src:"https://picsum.photos/530/300?random",title:"show6",content:"內容6",description:"細節6",uuid:"6",show:"fabric"},
          { src:"https://picsum.photos/540/300?random",title:"show7",content:"內容7",description:"細節7",uuid:"7",show:"fabric"},
          { src:"https://picsum.photos/550/300?random",title:"show8",content:"內容8",description:"細節8",uuid:"8",show:"fabric"},
        ],

    }),
    // created() {
    // },
    methods:{
        show(data){
            this.$router.push("/"+data.show)
        }
    },
  }
</script>